import React, { useEffect, useState } from 'react';

const apiBase = '/api';

// Componente do Formulrio de Processo
export default function ProcessoForm({ processoParaEditar, onFormSubmit, onCancel }) {
  const isEditing = !!processoParaEditar?.id;

  // Define um estado inicial "limpo" para garantir que todos os campos sejam strings controladas
  const blankState = {
    numero: '',
    autor: '',
    reu: '',
    uf: '',
    municipio_id: '',
    vara: '',
    fase: '',
    esfera_justica: '',
    categoria: '',
    tribunal_originario: '',
    tipo: '',
    rito: '',
    status: '',
    data_abertura: '',
    classe: '',
    sub_classe: '',
    observacoes: '',
    cliente_id: ''
  };

  // Inicializa o estado do formulrio
  // Se estiver editando, mescla os dados do processo com o estado limpo
  // Isso evita que valores `null` quebrem os inputs controlados
  const [formData, setFormData] = useState(() => {
    if (processoParaEditar) {
      const dados = { ...blankState, ...processoParaEditar };
      // Se tiver municipio como objeto, extrai a UF e o ID
      if (processoParaEditar.municipio && typeof processoParaEditar.municipio === 'object') {
        dados.uf = processoParaEditar.municipio.uf || '';
        dados.municipio_id = processoParaEditar.municipio_id || processoParaEditar.municipio.id || '';
      }
      return dados;
    }
    return blankState;
  });

  const [ufs, setUfs] = useState([]);
  const [municipios, setMunicipios] = useState([]);
  const [tipos, setTipos] = useState([]);
  const [categorias, setCategorias] = useState([]);
  const [tribunais, setTribunais] = useState([]);
  const [esferas, setEsferas] = useState([]);
  const [clientes, setClientes] = useState([]);
  const [classes, setClasses] = useState({});
  const [ritos, setRitos] = useState({});

  useEffect(() => {
    const fetchConfigData = async () => {
      try {
        const [ufsRes, classesRes, tiposRes, categoriasRes, tribunaisRes, esferasRes, ritosRes, clientesRes] = await Promise.all([
          fetch('/api/config/ufs'),
          fetch('/api/config/classes'),
          fetch('/api/config/tipos'),
          fetch('/api/config/categorias'),
          fetch('/api/config/tribunais'),
          fetch('/api/config/esferas'),
          fetch('/api/config/ritos'),
          fetch('/api/clientes') // Busca a lista de clientes
        ]);
        setUfs(await ufsRes.json());
        setClasses(await classesRes.json());
        setTipos(await tiposRes.json());
        setCategorias(await categoriasRes.json());
        setTribunais(await tribunaisRes.json());
        setEsferas(await esferasRes.json());
        const ritosData = await ritosRes.json();
        console.log('Ritos carregados:', ritosData);
        setRitos(ritosData);
        setClientes(await clientesRes.json()); // Armazena a lista de clientes no estado
      } catch (err) {
        console.error("Erro ao buscar dados de configurao:", err);
      }
    };
    fetchConfigData();
  }, []);

  // Carregar municpios quando UF for selecionada ou quando o componente for montado com processo para editar
  useEffect(() => {
    if (formData.uf) {
      const fetchMunicipios = async () => {
        try {
          const res = await fetch(`${apiBase}/municipios/uf/${formData.uf}`);
          if (!res.ok) throw new Error('Falha ao buscar municpios');
          const data = await res.json();
          setMunicipios(data);
        } catch (err) {
          console.error("Erro ao buscar municpios:", err);
          setMunicipios([]);
        }
      };
      fetchMunicipios();
    } else {
      setMunicipios([]);
    }
  }, [formData.uf]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    const newFormData = { ...formData, [name]: value };

    // Se a categoria mudar para 'Originrio'
    if (name === 'categoria' && value === 'Originrio') {
      // Limpa os campos de 'Comum'
      newFormData.uf = '';
      newFormData.municipio_id = '';
      newFormData.vara = '';
      // Define o tipo como Judicial e limpa classe/sub-classe
      newFormData.tipo = 'Judicial';
      newFormData.classe = '';
      newFormData.sub_classe = '';
    }

    // Se a categoria mudar para 'Comum'
    if (name === 'categoria' && value === 'Comum') {
      // Limpa os campos de 'Originrio' e reseta o tipo
      newFormData.tribunal_originario = '';
      newFormData.tipo = '';
    }

    // Se a UF mudar, limpa o municpio
    if (name === 'uf') {
      newFormData.municipio_id = '';
    }

    // NOVA REGRA: Se a classe for Criminal, o tipo  sempre Judicial.
    if (name === 'classe' && value === 'Criminal') {
      newFormData.tipo = 'Judicial';
    }

    // Se o tipo mudar para algo que no seja Judicial, e a classe for Criminal, limpa a classe.
    if (name === 'tipo' && value !== 'Judicial' && newFormData.classe === 'Criminal') {
      newFormData.classe = '';
      newFormData.sub_classe = '';
    }

    // --- NOVAS REGRAS DE ESFERA AUTOMTICA ---
    // Se a classe for Trabalhista, a esfera  Trabalhista
    if (name === 'classe' && value === 'Trabalhista') {
      newFormData.esfera_justica = 'Justia Trabalhista';
    }

    // Se a classe for Eleitoral, a esfera  Eleitoral
    if (name === 'classe' && value === 'Eleitoral') {
      newFormData.esfera_justica = 'Justia Eleitoral';
    }

    // Regras baseadas no tribunal originrio
    if (name === 'tribunal_originario') {
      if (value === 'TRT') newFormData.esfera_justica = 'Justia Trabalhista';
      else if (value === 'TRF') newFormData.esfera_justica = 'Justia Federal';
      else if (value === 'TRM') newFormData.esfera_justica = 'Justia Militar';
      else if (value === 'TRE') newFormData.esfera_justica = 'Justia Eleitoral';
      // Limpa a esfera se for um tribunal sem regra automtica (STJ, STF, TJ)
      else if (['STJ', 'STF', 'TJ'].includes(value)) {
        newFormData.esfera_justica = '';
      }
    }

    // Se a categoria mudar, limpa a esfera para reavaliao
    if (name === 'categoria') {
      newFormData.esfera_justica = '';
    }

    // Se a classe mudar, limpa a esfera para reavaliao (exceto para as que tem regra)
    if (name === 'classe' && !['Trabalhista', 'Eleitoral'].includes(value)) {
      newFormData.esfera_justica = '';
    }

    // Se a classe ou sub-classe mudar, limpa a fase para reavaliao
    if (name === 'classe' || name === 'sub_classe') {
      newFormData.fase = '';
    }

    // Se o tipo mudar e no for Judicial, limpa o rito
    if (name === 'tipo' && value !== 'Judicial') {
      newFormData.rito = '';
    }

    // Se a classe mudar, limpa o rito para reavaliao
    if (name === 'classe') {
      newFormData.rito = '';
    }

    // Se o rito for Militar, a esfera deve ser Justia Militar
    if (name === 'rito' && value === 'Militar') {
      newFormData.esfera_justica = 'Justia Militar';
    }

    // Se o rito for Terrorismo / Segurana Nacional, a esfera deve ser Justia Federal
    if (name === 'rito' && value === 'Terrorismo / Segurana Nacional') {
      newFormData.esfera_justica = 'Justia Federal';
    }

    // Se a esfera for Justia Militar e a classe for Criminal, sugere o rito Militar
    if (name === 'esfera_justica' && value === 'Justia Militar' && newFormData.classe === 'Criminal') {
      // No fora, mas se o rito estiver vazio, pode sugerir
      if (!newFormData.rito) {
        newFormData.rito = 'Militar';
      }
    }

    // Se a esfera for Justia Federal e a classe for Criminal, sugere o rito Terrorismo / Segurana Nacional
    if (name === 'esfera_justica' && value === 'Justia Federal' && newFormData.classe === 'Criminal') {
      if (!newFormData.rito) {
        newFormData.rito = 'Terrorismo / Segurana Nacional';
      }
    }

    // Se mudar o rito de Militar para outro e a esfera for Militar, limpa a esfera
    if (name === 'rito' && formData.rito === 'Militar' && value !== 'Militar' && newFormData.esfera_justica === 'Justia Militar') {
      newFormData.esfera_justica = '';
    }

    // Se o tribunal for TRF ou TJ e a classe for Trabalhista, limpa a classe
    if (name === 'tribunal_originario' && ['TRF', 'TJ'].includes(value) && newFormData.classe === 'Trabalhista') {
      newFormData.classe = '';
      newFormData.sub_classe = '';
    }

    setFormData(newFormData);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    const url = isEditing ? `${apiBase}/processos/${processoParaEditar.id}` : `${apiBase}/processos`;
    const method = isEditing ? 'PUT' : 'POST';

    // Funo para limpar o objeto de dados, removendo chaves com valores nulos ou vazios.
    // Isso evita enviar campos opcionais vazios que podem falhar na validao do backend (ex: Enums).
    const cleanData = (obj) => {
      const newObj = {};
      for (const key in obj) {
        if (obj[key] !== null && obj[key] !== '') {
          // Converte municipio_id e cliente_id para inteiro se necessrio
          if ((key === 'municipio_id' || key === 'cliente_id') && typeof obj[key] === 'string') {
            newObj[key] = parseInt(obj[key], 10);
          } else {
            newObj[key] = obj[key];
          }
        }
      }
      // Remove o campo 'municipio' se existir (vem do backend, mas no deve ser enviado de volta)
      delete newObj.municipio;
      delete newObj.cliente;
      return newObj;
    };
    try {
      const response = await fetch(url, {
        method,
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(cleanData(formData)),
      });
      if (!response.ok) {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'Falha ao salvar processo');
      }
      onFormSubmit();
    } catch (err) {
      console.error(err);
      alert(`Erro ao salvar processo: ${err.message}`);
    }
  };

  const subClasses = classes[formData.classe] || [];

  // Filtra as classes disponveis com base no tipo selecionado
  let availableClasses = Object.keys(classes);

  // Regra 1: Se o tipo no for Judicial, remove a classe Criminal
  if (formData.tipo && formData.tipo !== 'Judicial') {
    availableClasses = availableClasses.filter(c => c !== 'Criminal');
  }

  // Regra 2: Se o tipo no for Judicial, remove a classe Eleitoral
  if (formData.tipo && formData.tipo !== 'Judicial') {
    availableClasses = availableClasses.filter(c => c !== 'Eleitoral');
  }
  // Regra 2: Se for Originrio de TRF ou TJ, remove a classe Trabalhista
  if (formData.categoria === 'Originrio' && ['TRF', 'TJ'].includes(formData.tribunal_originario)) {
    availableClasses = availableClasses.filter(c => c !== 'Trabalhista');
  }

  // --- LGICA DE EXIBIO DA ESFERA DE JUSTIA ---

  // 1. Filtra as esferas disponveis com base nas regras
  let availableEsferas = [...esferas];

  // Regra da Esfera Militar: s aparece se a classe for Criminal OU se o rito for Militar
  if (formData.classe !== 'Criminal' && formData.rito !== 'Militar') {
    availableEsferas = availableEsferas.filter(e => e !== 'Justia Militar');
  }
  // Se o rito for Militar, s mostra Justia Militar
  if (formData.rito === 'Militar') {
    availableEsferas = availableEsferas.filter(e => e === 'Justia Militar');
  }

  // Regra da Esfera Trabalhista: s aparece nos cenrios corretos
  const isComumTrabalhista = formData.categoria === 'Comum' && formData.tipo === 'Judicial' && formData.classe === 'Trabalhista';
  const isOriginarioTrabalhista = formData.categoria === 'Originrio' && ['TRT', 'STJ', 'STF'].includes(formData.tribunal_originario);
  if (!isComumTrabalhista && !isOriginarioTrabalhista) {
    availableEsferas = availableEsferas.filter(e => e !== 'Justia Trabalhista');
  }

  // Regra da Esfera Eleitoral
  const isComumEleitoral = formData.categoria === 'Comum' && formData.tipo === 'Judicial' && formData.classe === 'Eleitoral';
  const isOriginarioEleitoral = formData.categoria === 'Originrio' && formData.tribunal_originario === 'TRE';
  if (!isComumEleitoral && !isOriginarioEleitoral) {
    availableEsferas = availableEsferas.filter(e => e !== 'Justia Eleitoral');
  }

  // 2. Define se o campo de seleo manual da Esfera deve ser exibido
  // Exibe para processos Judiciais (Comum ou Originrio) quando as condies forem atendidas
  const deveExibirEsfera = (
    // Cenrio Comum - qualquer processo judicial
    (formData.categoria === 'Comum' && formData.tipo === 'Judicial' && formData.classe) ||
    // Cenrio Originrio - STJ, STF, TJ, TRF, TRE, TRT
    (formData.categoria === 'Originrio' && formData.tribunal_originario)
  );

  // --- LGICA DE EXIBIO DAS FASES ---
  // Comea com as fases padro, sem "Arquivado"
  let availablePhases = ["Inicial", "Recursal"];
  const classesParaCumprimento = ['Cvel', 'Trabalhista', 'Tributrio', 'Empresarial', 'Previdencirio'];

  // Regra para "Cumprimento de Sentena"
  if (
    classesParaCumprimento.includes(formData.classe) ||
    formData.sub_classe === 'Eleitoral Cvel'
  ) {
    availablePhases.push("Cumprimento de Sentena");
  }

  // Regra para "Execuo da Pena"
  if (formData.classe === 'Criminal' || formData.sub_classe === 'Eleitoral Criminal') {
    availablePhases.push("Execuo da Pena");
  }

  // Garante que "Arquivado" seja sempre a ltima opo da lista
  availablePhases.push("Arquivado");


  return (
    <div className="card">
      <h3>{isEditing ? 'Editar Processo' : 'Novo Processo'}</h3>
      <form onSubmit={handleSubmit} style={{ display: 'grid', gridTemplateColumns: '1fr 1fr', gap: '10px' }}>
        <input name="numero" value={formData.numero} onChange={handleChange} placeholder="Nmero do Processo" />
        <input name="autor" value={formData.autor} onChange={handleChange} placeholder="Autor" />
        <input name="reu" value={formData.reu} onChange={handleChange} placeholder="Ru" />
        <select name="categoria" value={formData.categoria} onChange={handleChange}>
          <option value="">Selecione a Categoria</option>
          {categorias.map(cat => <option key={cat} value={cat}>{cat}</option>)}
        </select>

        {/* Campos para Processo Originrio */}
        {formData.categoria === 'Originrio' && (
          <select name="tribunal_originario" value={formData.tribunal_originario} onChange={handleChange}>
            <option value="">Selecione o Tribunal</option>
            {tribunais.map(t => <option key={t} value={t}>{t}</option>)}
          </select>
        )}

        {/* Campos de Classe/Sub-classe para Tribunais especficos em Originrio */}
        {formData.categoria === 'Originrio' && ['STJ', 'STF', 'TJ', 'TRF', 'TRE', 'TRT', 'TRM'].includes(formData.tribunal_originario) && (
          <>
            <select name="classe" value={formData.classe} onChange={handleChange}>
              <option value="">Selecione a Classe</option>
              {availableClasses.map(c => <option key={c} value={c}>{c}</option>)}
            </select>
            {subClasses.length > 0 && (
              <select name="sub_classe" value={formData.sub_classe} onChange={handleChange}><option value="">Selecione a Sub-classe</option>{subClasses.map(sub => <option key={sub} value={sub}>{sub}</option>)}</select>
            )}
            {/* Campo de Rito para Originrio - s aparece para Cvel ou Criminal */}
            {['Cvel', 'Criminal'].includes(formData.classe) && (
              <select name="rito" value={formData.rito} onChange={handleChange}>
                <option value="">Selecione o Rito</option>
                {(ritos[formData.classe] || []).map(r => <option key={r} value={r}>{r}</option>)}
              </select>
            )}
          </>
        )}

        {/* Campos para Processo Comum */}
        {formData.categoria === 'Comum' && (
          <>
            <select name="uf" value={formData.uf} onChange={handleChange}>
              <option value="">Selecione a UF</option>
              {ufs.map(uf => <option key={uf} value={uf}>{uf}</option>)}
            </select>
            {formData.uf && (
              <select name="municipio_id" value={formData.municipio_id} onChange={handleChange} required>
                <option value="">Selecione o Municpio</option>
                {municipios.map(m => <option key={m.id} value={m.id}>{m.nome}</option>)}
              </select>
            )}
            <select name="tipo" value={formData.tipo} onChange={handleChange} disabled={formData.classe === 'Criminal'}>
              <option value="">Selecione o Tipo</option>
              {tipos.map(t => <option key={t} value={t}>{t}</option>)}
            </select>
            <input name="vara" value={formData.vara} onChange={handleChange} placeholder="Vara/Juzo" />
            <select name="classe" value={formData.classe} onChange={handleChange}>
              <option value="">Selecione a Classe</option>
              {availableClasses.map(c => <option key={c} value={c}>{c}</option>)}
            </select>
            {subClasses.length > 0 && (
              <select name="sub_classe" value={formData.sub_classe} onChange={handleChange}>
                <option value="">Selecione a Sub-classe</option>
                {subClasses.map(sub => <option key={sub} value={sub}>{sub}</option>)}
              </select>
            )}
            {/* Campo de Rito - sempre para qualquer classe Judicial (fallback 'Comum') */}
            {formData.tipo === 'Judicial' && formData.classe && (
              <select name="rito" value={formData.rito} onChange={handleChange}>
                <option value="">Selecione o Rito</option>
                {(Array.isArray(ritos[formData.classe]) && ritos[formData.classe].length > 0 ? ritos[formData.classe] : ['Comum']).map(r => (
                  <option key={r} value={r}>{r}</option>
                ))}
              </select>
            )}
          </>
        )}

        {/* Campo de Esfera de Justia - exibido para processos judiciais */}
        {deveExibirEsfera && (
          <select name="esfera_justica" value={formData.esfera_justica} onChange={handleChange}>
            <option value="">Selecione a Esfera de Justia</option>
            {availableEsferas.map(e => <option key={e} value={e}>{e}</option>)}
          </select>
        )}

        <select name="fase" value={formData.fase} onChange={handleChange}>
          <option value="">Selecione a Fase</option>
          {availablePhases.map(f => <option key={f} value={f}>{f}</option>)}
        </select>
        <select name="status" value={formData.status} onChange={handleChange}>
          <option value="">Selecione o Status</option>
          <option value="Ativo">Ativo</option>
          <option value="Encerrado">Encerrado</option>
        </select>
        <div>
          <label htmlFor="data_abertura" style={{ fontSize: '0.9em', color: '#555' }}>
            Data da Distribuio
          </label>
          <input id="data_abertura" name="data_abertura" value={formData.data_abertura} onChange={handleChange} type="date" style={{ width: '100%', boxSizing: 'border-box' }} />
        </div>
        <select name="cliente_id" value={formData.cliente_id} onChange={handleChange} style={{ gridColumn: '1 / -1' }}>
          <option value="">Selecione um Cliente</option>
          {clientes.map(cliente => (
            <option key={cliente.id} value={cliente.id}>{cliente.nome} (CPF/CNPJ: {cliente.cpf_cnpj})</option>
          ))}
        </select>
        <textarea name="observacoes" value={formData.observacoes} onChange={handleChange} placeholder="Observaes" style={{ gridColumn: '1 / -1', minHeight: '80px' }} />
        <div>
          <button type="submit" className="btn btn-primary">Salvar</button>
          <button type="button" onClick={onCancel} className="btn btn-secondary" style={{ marginLeft: '10px' }}>Cancelar</button>
        </div>
      </form>
    </div>
  );
}
